@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
  line-height: inherit;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 90vw;
  max-width: 1200px;
  margin: 0 auto;
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
}
.title--secondary {
  color: #e2b65c;
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  min-width: 40px;
  min-height: 40px;
  max-width: max-content;
  text-decoration: none;
  border: none;
  background-color: #111111;
  color: #ffffff;
  border-radius: 8px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  padding: 4px 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  transition: 0.3s;
  cursor: pointer;
}
.button:disabled {
  opacity: 0.6;
}
.button--secondary {
  background-color: #e2b65c;
}
.button--gray {
  background-color: #B2B2B2;
}
@media (min-width: 1200px) {
  .button:hover {
    background-color: #e2b65c;
  }
}

.text {
  margin-top: 0;
  margin-bottom: 0;
  color: #000000;
  line-height: 24px;
  font-size: 16px;
}
.text--error {
  color: #DC0000;
}
.text--success {
  color: #66bb6a;
}

.page {
  font-family: "Roboto", sans-serif;
  height: 100%;
  font-size: 16px;
  line-height: 1.4;
  color: #111111;
}
.page::-webkit-scrollbar {
  width: 8px;
}
.page::-webkit-scrollbar-track {
  background-color: #e2b65c;
}
.page::-webkit-scrollbar-thumb {
  background-color: #111111;
}

.page__body {
  min-height: 100%;
  min-width: 320px;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  gap: 16px;
  background-color: #f4f4f4;
}
@media (min-width: 768px) {
  .page__body {
    gap: 24px;
  }
}

.main-logo {
  display: flex;
}

.main-navigation {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.menu-hidden .main-navigation {
  display: none;
  visibility: hidden;
}
@media (min-width: 768px) {
  .main-navigation {
    width: auto;
    flex-direction: row;
    gap: 0;
  }
}
@media (min-width: 1200px) {
  .main-navigation {
    gap: 8px;
  }
}

.main-navigation__link {
  display: flex;
  text-decoration: none;
  background-color: transparent;
  border: none;
  padding: 0;
  gap: 8px;
  min-height: 40px;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  cursor: pointer;
}
.main-navigation__link--current {
  opacity: 0.4;
}
@media (min-width: 768px) {
  .main-navigation__link {
    padding: 4px 24px;
    border-radius: 8px;
  }
  .main-navigation__link:hover {
    background-color: #e2b65c;
  }
  .main-navigation__link--current {
    opacity: 1;
    background-color: #e2b65c;
  }
  .main-navigation__link--login, .main-navigation__link--account {
    display: none;
  }
}

.page-footer {
  background-color: #111111;
  color: #ffffff;
  padding: 16px 0;
}
@media (min-width: 768px) {
  .page-footer {
    padding: 24px 0;
  }
}
@media (min-width: 1200px) {
  .page-footer {
    padding: 32px 0;
  }
}

.page-footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

