  text-decoration: none;
  background-color: transparent;
  border: none;
  padding: 0;
  gap: 8px;
  min-height: 40px;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  cursor: pointer;
  display: none;
}
@media (min-width: 768px) {
  .login-link {
    display: flex;
    padding: 4px 24px;
    border-radius: 8px;
  }
  .login-link:hover {
    background-color: #e2b65c;
  }
}

.account-link {
  text-decoration: none;
  background-color: transparent;
  border: none;
