.page-header {
  background-color: #111111;
  color: #ffffff;
  padding: 16px 0;
}
@media (min-width: 768px) {
  .page-header {
    padding: 24px 0;
  }
}
@media (min-width: 1200px) {
  .page-header {
    position: sticky;
    top: 0;
    z-index: 2;
  }
}

.page-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.page-header__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  color: #e2b65c;
}

.pagination {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
}

.pagination__item {
  display: flex;
}
.pagination__item:nth-last-child(2) {
  transform: scaleX(-1);
}

.pagination__link {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  padding: 1px 1px 0 0;
  width: 24px;
  min-width: 24px;
  height: 24px;
  border-radius: 4px;
  background-color: #ffffff;
  text-decoration: none;
  transition: 0.3s;
}
.pagination__link--current {
  font-weight: 500;
  background-color: #111111;
  color: #ffffff;
  pointer-events: none;
}
.pagination__link--disabled {
  color: #b2b2b2;
}
.pagination__item:nth-last-child(2) .pagination__link, .pagination__item:last-child .pagination__link {
  padding: 0;
}
@media (min-width: 1200px) {
  .pagination__link:hover {
    background-color: #e2b65c;
    color: #ffffff;
  }
}

.post-card {
  display: flex;
  min-width: max-content;
}

.post-card__image {
  border-radius: 4px;
}

.post-card {
  display: flex;
  min-width: max-content;
}

.post-card__image {
  border-radius: 4px;
}

.posts {
  padding: 16px;
  background-color: #ffffff;
  border-radius: 8px;
}
@media (min-width: 768px) {
  .posts {
    min-width: max-content;
    height: calc(100vh - 48px);
    position: sticky;
    top: 24px;
    z-index: 0;
    padding-right: 6px;
  }
}
@media (min-width: 1200px) {
  .posts {
    top: 112px;
    height: calc(100vh - 136px);
  }
}

.posts__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 8px;
  overflow-x: scroll;
  overflow-y: hidden;
}
@media (min-width: 768px) {
  .posts__list {
    flex-direction: column;
    max-height: 100%;
    padding-right: 6px;
    overflow-x: hidden;
    overflow-y: scroll;
  }
  .posts__list::-webkit-scrollbar {
    width: 4px;
  }
  .posts__list::-webkit-scrollbar-thumb {
    background-color: #e2b65c;
    border-radius: 2px;
  }
}

.quote-card {
  margin: 0;
  background-color: #ffffff;
  border-radius: 8px;
}
@media (min-width: 768px) {
  .quote-card {
    flex-grow: 1;
  }
}

.quote-card__link {
  text-decoration: none;
  float: right;
  border-radius: 0 8px 0 8px;
  background-color: #b2b2b2;
  color: #ffffff;
  font-size: 14px;
  line-height: 1;
  padding: 4px 8px;
  margin-left: 16px;
  margin-bottom: 16px;
}

.quote-card__top {
  padding: 8px 16px;
}
@media (min-width: 768px) {
  .quote-card__top {
    padding: 16px 24px;
  }
}

.quote-card__quote {
  quotes: none;
  display: block;
  margin-bottom: 8px;
  white-space: pre-wrap;
}

.quote-card__tags {
  font-size: 14px;
  color: #747474;
  display: flex;
  flex-wrap: wrap;
  gap: 4px 16px;
}

.quote-card__tag {
  text-decoration: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  transition: 0.3s;
}
.tags-hidden .quote-card__tag:nth-child(n+4) {
  visibility: hidden;
  display: none;
}
.quote-card__tag--current {
  font-weight: bold;
}
@media (min-width: 1200px) {
  .quote-card__tag:hover {
    color: #e2b65c;
  }
}

.quote-card__bottom {
  border-top: 1px solid #b2b2b2;
  padding: 8px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}
@media (min-width: 768px) {
  .quote-card__bottom {
    padding: 8px 24px;
    gap: 24px;
  }
}

.quote-card__buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 16px;
  color: #b2b2b2;
}

.quote-card__button {
  text-decoration: none;
  border: none;
  background-color: transparent;
  padding: 0;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
}
.quote-card__button svg {
  fill: none;
}
.quote-card__button--toggle-tags {
  color: #e2b65c;
}
.quote-card__button--toggle-tags::before {
  content: attr(data-hide-text);
}
.tags-hidden .quote-card__button--toggle-tags::before {
  content: attr(data-show-text);
}
.quote-card__button--favorite {
  color: #e2b65c;
}
.quote-card__button--favorite svg {
  fill: #e2b65c;
}

.quote-card__button-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
}

.quote-card__share {
  position: relative;
  z-index: 1;
  max-width: 24px;
  width: 24px;
  height: 24px;
  margin-left: auto;
}

.quote-card__share-links {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 8px;
  right: 100%;
  background-color: #ffffff;
  display: flex;
  gap: 8px;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
}
.quote-card__share:hover .quote-card__share-links {
  visibility: visible;
  opacity: 1;
}

.quote-card__share-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  background-color: #e2b65c;
  padding: 0;
  border-radius: 4px;
  border: none;
  color: #ffffff;
  cursor: pointer;
}

