.search-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100vw;
  height: 100vh;
  background-color: #111111;
  border: none;
  padding: 0;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  pointer-events: all;
}
.search-modal-shown .search-modal {
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .search-modal {
    background-color: rgba(17, 17, 17, 0.8);
  }
}
@media (min-width: 1200px) {
  .search-modal {
    padding-top: 16vh;
  }
}

.search-modal-shown {
  overflow: hidden;
  pointer-events: none;
}

.search-modal__title {
  color: #ffffff;
  padding: 16px;
}

